<template>
    <div class="user">
        <van-skeleton
                title
                :row="3"
                :loading="loading">
            <van-row class="user-poster">
                <van-col span="8">
                    <div class="poster">
                        <img :src="userInfo.headimgurl" alt="">
                    </div>
                    <div>{{userInfo.nickname}}</div>
                </van-col>
                <van-col span="16">
                    <van-row class="own-name-line">
                        <!--<div>{{userInfo.nickname}}</div>-->
                        <div class="poster-name">Fans ZX</div>
                    </van-row>
                </van-col>
            </van-row>
        </van-skeleton>

    </div>
</template>

<script>
    import {mapGetters} from "vuex";
    import {Row, Col, Icon, Cell, CellGroup} from 'vant';

    export default {
        components: {
            [Row.name]: Row,
            [Col.name]: Col,
            [Icon.name]: Icon,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup
        },
      data() {
        return {
          loading: false,
          orderId: '',
          readInfo: ''
        }
      },
        computed: {
            ...mapGetters(["userInfo"])
        },
        created() {
          this.loading = true;
        },
        mounted() {
          // this.$store.state.user.userInfo
            console.log( this.userInfo)
          this.getUserReadInfo();
          this.loading = false;

        },
        methods: {
            logOut() {
                this.$dialog.confirm({
                    message: '确认要退出登录吗？'
                }).then(() => {
                    // on confirm
                    localStorage.clear();
                    this.$router.replace('/logout')
                }).catch(() => {
                    // on cancel
                });

            },
          async getUserReadInfo() {
            let response = await this.apis.user.getUserReadInfo({openid: this.userInfo.openid});
            console.log(response)
            if(response.code === 200) {
              this.readInfo = response.data;
            }
          }
        }
    };
</script>

<style lang="less">

    .user {
        width: 100%;
        &-poster {
             padding-top: 3vw;
             background: cornflowerblue;
            .poster{
                padding: 3vw 6.5vw;
                width: 100%;
                img{
                    border-radius: 50%;
                }
            }
            .own-name-line{
                margin-top: 10vw;
                text-align: left;
                .poster-name{
                    font-size: 5vw;
                    font-weight: 600;
                }
            }
        }

        &-attribute {
            height: 24vw;
            padding: 4vw;
            border-radius: 5vw;
            /*background: #3dcc97;*/
            .own-keep {
                font-size: 6vw;
                color: #1cb5cc;
                height: 10vw;
                line-height: 10vw;
            }
            .own-keep-b{
                font-size: 4vw;
                color: #1cb5cc;
                height: 10vw;
                line-height: 10vw;

            }
        }

        &-group {
            margin-bottom: 15px;
        }
        &-links {
            padding: 15px 0;
            font-size: 12px;
            text-align: center;
            background-color: #fff;
            .van-icon {
                display: block;
                font-size: 24px;
            }
        }
    }
</style>